export abstract class VAL {
  static _translate
  static ENTER = (val) => {
    return {
      key: "ENTER",
      lbl: val,
      en: 'Please enter ' + val,
      ur: '  '.concat(' ', this._translate?.instant(val), ' ', ' ')
    }
  }
  static SELECT = (val) => {
    return {
      key: "SELECT",
      lbl: val,
      en: 'Please select ' + val,
      ur: '  '.concat(' ', this._translate.instant(val), ' ', ' ')
    }
  }
  static WHITE_SPACE = {
    key: "WHIE_SPACE",
    en: 'White space not allowed',
    ur: '     '
  }
  static MAX_CHAR = (val) => {
    return {
      key: "MAX_CHAR",
      en: 'Maximum ' + val + ' characters allowed',
      ur: '   '.concat(' ', val, ' ', '   ')
    }
  }
  static MIN_CHAR = (val) => {
    return {
      key: "MIN_CHAR",
      en: 'Minimum ' + val + ' characters allowed',
      ur: '   '.concat(' ', val, ' ', '   ')
    }
  }
  static NUM = {
    key: "NUM",
    en: 'Only numbers allowed',
    ur: '    '
  }
  static DECIMAL = {
    key: "DECIMAL",
    en: 'Only whole numbers allowed',
    ur: '     '
  }
  static MIN = (val) => {
    return {
      key: "MIN",
      en: 'Minimum digits ' + val + ' allowed',
      ur: '   '.concat(' ', val, ' ', '   ')
    }
  }
  static MAX = (val) => {
    return {
      key: "MAX",
      en: 'Maximum digits ' + val + ' allowed',
      ur: '   '.concat(' ', val, ' ', '   ')
    }
  }
  static NUM_POS = {
    key: "NUM_POS",
    en: 'Only positive numbers allowed',
    ur: '     '
  }
  static ALPHA = {
    key: "ALPHA",
    en: 'Only alphabets allowed',
    ur: '      '
  }
  static ALPHANUM = {
    key: "ALPHANUM",
    en: 'Only alphabets and numbers allowed',
    ur: '          '
  }
  static HYPHEN = {
    key: "HYPHEN",
    en: 'Hyphen not allowed at start and end',
    ur: '     (-)    '
  }
  static PATTERN = {
    key: "PATTERN",
    en: 'Special characters not allowed',
    ur: '     '
  }
  static EMAIL = {
    key: "EMAIL",
    en: 'Invalid email containing "@, .com"',
    ur: '    '.concat(' ', 'abc@xyz.com', '    ')
  }
  static PASSWORD = {
    key: "PASSWORD",
    en: 'Invalid password must contains Upper Case, Lower Case, Number and Special Character.',
    ur: '            '
  }
  static DATE_EQUAL = {
    key: "DATE_EQUAL",
    en: 'Date must be equal current date',
    ur: '       '
  }
  static MIN_DATE = {
    key: "MIN_DATE",
    en: 'Date must be <= current date',
    ur: '         '
  }
  static MAX_DATE = {
    key: "MAX_DATE",
    en: 'Date must be >= current date',
    ur: '         '
  }
  static CONFIRM = {
    key: "CONFIRM",
    en: 'Please enter Confirm Password',
    ur: '      '
  }
  static MATCH = {
    key: "MATCH",
    en: 'Your passwords are not match',
    ur: '       '
  }
  static DUPLICATE = {
    key: "DUPLICATE",
    en: 'Duplicate Selection Not Allowed',
    ur: '     '
  }
}
